<template>
  <div class="row">
    <div class="col-md-12">
      <alert 
        :alert="alert"
      />
      <div class="col-md-6 col-sm-6">
        <router-link 
          v-if="events.length !== 0"
          class="btn btn-primary btn-link" 
          to="/events/add"
        >
          Add new event
        </router-link>
      </div>
      <div class="col-md-6 col-sm-6">
        <fg-input
          @changeValue="globalEventSearch"
          placeholder="Search value"
          :value="searchValue"
        />
      </div>
      <div class="clearfix"></div>
   </div>
   <div class="col-md-12">
      <div class="card" v-if="events.length !== 0">
        <paper-table 
          :title="table.title" 
          :sub-title="table.subTitle" 
          :data="table.data" 
          :columns="table.columns"
          :actions="table.actions"
          :handleOrder="orderEvents"
          @clickDelete="handleDeleteEvent"
        />
        <button
          class="btn btn-primary pull-right newUserButton"
          @click="showMoreEvents"
          v-if="isMoreAvialiable || count > 10"
        >
          Show more
        </button>
      </div>
      <jumbotron 
        title="Ups nothing"
        text="Please add more events"
        v-if="events.length === 0"
      >
        <router-link 
          class="btn btn-primary" 
          to="/events/add"
        >
          Add new event
        </router-link>
      </jumbotron>
    </div>
  </div>
</template> 

<script>
  import { mapGetters, mapActions, mapState } from 'vuex'
  import { tableColumns, tableActions } from './TableMock.js'
  import PaperTable from 'components/UIComponents/PaperTable.vue'
  export default {
    components: {
      PaperTable
    },
    data: () => ({
      tableActions,
      tableColumns,
      table: {
        title: 'Events list',
        subTitle: 'Events existing list',
        columns: [...tableColumns],
        actions: [...tableActions],
        data: []
      }
    }),
    mounted() {
      this.fetchEvents()
    },
    watch: {
      events (value) {
        if (value) {
          this.table.data = [
            ...value
          ]
        }
      }
    },
    computed: {
      ...mapGetters({
        events: 'getEvents',
        isMoreAvialiable: 'isMoreAvialiable'
      }),
      ...mapState({
        alert: state => state.alert,
        user: state => state.user,
        count: ({ events: { pagination } }) => pagination.count,
        searchValue: ({ events: { searchValue } }) => searchValue
      })
    },
    methods: {
      handleDeleteEvent(value) {
        const id = value
        this.removeEvent({ id })
      },
      ...mapActions([
        'fetchEvents',
        'removeEvent',
        'showMoreEvents',
        'globalEventSearch',
        'orderEvents'
      ])
    }
  }
</script>

<style lang="css" scoped>
  .newUserButton {
    margin: 10px 0px;
    display: block;
    width: 100%;
  }
  .alert {
    margin: 0px 0px 10px;
  }
  .btn-link {
    margin-bottom: 15px;
  }



