<template>
  <div>
    <div class="header">
      <button
        class="btn order-btn"
        @click="updateItems"
      >
        Reorder
      </button>
      <slot name="header">
        <h4 class="title">{{title}}</h4>
        <p class="category">{{subTitle}}</p>
      </slot>
    </div>
    <div class="content table-responsive table-full-width">
      <table class="table" :class="tableClass">
        <thead>
          <th>#</th>
          <th v-for="(column, key) in columns" :key="key">
            <span>{{column.title}}</span>
          </th>
          <th>Actions</th>
        </thead>
        <tbody ref="sortableTable">
          <tr
            v-for="(item, key) in items"
            :key="itemKey(item)"
            class="sortableRow"
          >
            <td class="sortHandle">
              <span class="ti-layers" />
              {{ key }}
            </td>
            <td
              v-for="(column, key) in columns"
              :key="key"
            >
              {{ column.field === 'createdAt' ?
                returnDate(item[column.field]) :
                item[column.field]
              }}
            </td>
            <td>
              <template v-for="(action, key) in actions" >
                <button 
                  :key="key"
                  v-if="action.tag === 'button' && action.dynamic"
                  type="button"
                  :class="action.class"
                  @click="$emit(action.emit, item._id)"
                >
                  {{ action.label }}
                </button>
                <button 
                  :key="key"
                  v-if="action.tag === 'button' && !action.dynamic"
                  type="button"
                  :class="action.class"
                  @click="$emit(action.emit, item[action.urlType])"
                >
                  {{ action.label }}
                </button>
                <router-link
                  :key="key"
                  v-if="action.tag === 'link'" 
                  :class="action.class"
                  :to="`${action.to}/${item[action.urlType]}`"
                >
                  {{ action.label }}
                </router-link>
              </template>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
  import Sortable from 'sortablejs'
  import moment from 'moment'

  export default {
    props: {
      columns: Array,
      actions: {
        type: Array,
        default: []
      },
      data: Array,
      type: {
        type: String, // striped | hover
        default: 'striped'
      },
      title: {
        type: String,
        default: ''
      },
      subTitle: {
        type: String,
        default: ''
      },
      handleOrder: {
        type: Function,
        default: () => {}
      }
    },
    data: () => ({
      itemKeys: new WeakMap(),
      currentItemKey: 0
    }),
    mounted() {
      new Sortable( // eslint-disable-line
        this.$refs.sortableTable,
        {
          draggable: '.sortableRow',
          handle: '.sortHandle',
          onEnd: this.dragReorder
        }
      )
    },
    computed: {
      tableClass () {
        return `table-${this.type}`
      },
      items: {
        get() {
          const elements = [...this.data]
          return elements
        },
        set(newItems) {
          return newItems
        }
      }
    },
    methods: {
      returnDate(date) {
        return moment(date).format('YYYY-MM-DD')
      },
      dragReorder(order) {
        const { oldIndex, newIndex } = order
        const element = this.items[oldIndex]
        this.items = this.items.splice(oldIndex, 1)
        this.items = this.items.splice(newIndex, 0, element)
      },
      updateItems() {
        const updatedOrder = this.items.map((item, key) => ({
          ...item,
          order: key
        }))
        this.handleOrder(updatedOrder)
      },
      itemKey (item) {
        if (!this.itemKeys.has(item)) this.itemKeys.set(item, ++this.currentItemKey)
        return this.itemKeys.get(item)
      },
      hasValue (item, column) {
        return item[column.toLowerCase()] !== 'undefined'
      }
    }
  }

</script>
<style lang="stylus" scoped>
  th, td
    text-align center !important

  .sortHandle
    cursor pointer

  .order-btn
    float right
</style>



